<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
        lang: localStorage.getItem("lang") || "en",
      link: "--",
      coinBalance: 0,
      isApprove: false,
      stakeAmount: 0,
      stakeAmount2: 0,
      tabsNum: "hby",
      timer: null,
      timer2: null,
      TokenPrice: 0,
      show: false,
      isApproveCHE:false,
      coinBalanceCHE:0,
      show1:false
    };
  },
  computed: {
    ...mapState("main", [
      "account",
      "HOYinfo",
      "HbyCoinContract",
      "CheCoinContract",
      "HbyDonationContract",
      "HbyDonationInfo",
      "CheDonationContract",
      "CheDonationInfo",
    ]),
  },
  methods: {
    ...mapActions("main", [
      // "add_CHEInfoPool",
      "add_HOYCoinContract",
      "add_HbyCoinContract",
      "add_HbyDonationContract",
      "add_CheDonationContract",
      "add_CheCoinContract"
    ]),
    changeTab: function (type) {
      this.tabsNum = type;
    },
    TipsShow:function(){
      this.show1=true;
    },
    clos: function () {
      this.show = false;
       this.show1=false;
      var myDate = Date.now();
      localStorage.setItem("lastTime", myDate);
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
    withdrawProfitHBY: function () {
      if (this.HbyDonationContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.HbyDonationContract.methods
        .withdrawProfit()
        .send({ from: account })
        .then((res) => {
          console.log("getReward", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_HbyDonationContract();
          _this.add_HOYCoinContract();
          _this.getCoinBalance();
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },

    //
    getCoinBalance: function () {
      if (this.HbyCoinContract) {
        var account = this.account;
        this.HbyCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 2 + index + 1);
            } else {
              num = num.substring(0);
            }
            this.coinBalance = res;
          });
      }
    },
    lpApprove: function () {
      if (this.HbyCoinContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.HbyDonationInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.HbyCoinContract.methods
        .approve(address, "50000000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 
    isApproveFun: function () {
      var account = this.account;
      var address = this.HbyDonationInfo.address;

      this.HbyCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log("usdt=========isApproveFun", account, address);
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
    stake: function () {
      if (this.HbyDonationContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;

      if (!_this.isApprove) {
        _this.$toast(_this.$t("message.noAccredit"));
        return;
      }
      if (Number(_this.stakeAmount) > Number(_this.coinBalance)) {
        _this.$toast(_this.$t("message.noBanner"));
        return;
      }
      if (_this.balance < 0.01) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      if (Number(_this.stakeAmount) <= 0) {
        _this.$toast(_this.$t("message.noNum"));
        return;
      }
      var monye = (_this.stakeAmount * 10 ** 18).toString();
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(
        "investIn=======",
        web3.utils.toWei(_this.stakeAmount.toString(), "ether")
      );
      this.HbyDonationContract.methods
        .investIn(web3.utils.toWei(_this.stakeAmount.toString(), "ether"))
        .send({
          from: account,
        })
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_HbyDonationContract();
          _this.add_HOYCoinContract();
          _this.getCoinBalance();

          _this.stakeAmount = "";
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },

 withdrawProfitCHE: function () {
      if (this.CheDonationContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.CheDonationContract.methods
        .withdrawProfit()
        .send({ from: account })
        .then((res) => {
          console.log("withdrawProfitCHE", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_CheDonationContract();
          _this.add_HOYCoinContract();
          _this.getCoinBalanceCHE();
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
