  //
    getCoinBalanceCHE: function () {
      if (this.CheCoinContract) {
        var account = this.account;
        this.CheCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 2 + index + 1);
            } else {
              num = num.substring(0);
            }
            this.coinBalanceCHE = res;
          });
      }
    },

    lpApproveCHE: function () {
      if (this.CheCoinContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.CheDonationInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.CheCoinContract.methods
        .approve(address, "5000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApproveCHE = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 
    isApproveFunCHE: function () {
      var account = this.account;
      var address = this.CheDonationInfo.address;

      this.CheCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log("usdt=========isApproveFun", account, address);
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApproveCHE = true;
          }
        });
    },
    stakeCHE: function () {
      if (this.CheDonationContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;

      if (!_this.isApproveCHE) {
        _this.$toast(_this.$t("message.noAccredit"));
        return;
      }
      if (Number(_this.stakeAmount2) > Number(_this.coinBalanceCHE)) {
        _this.$toast(_this.$t("message.noBanner"));
        return;
      }
      if (_this.balance < 0.01) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      if (Number(_this.stakeAmount2) <= 0) {
        _this.$toast(_this.$t("message.noNum"));
        return;
      }
      var monye = (_this.stakeAmount2 * 10 ** 18).toString();
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(
        "investIn=======",
        web3.utils.toWei(_this.stakeAmount2.toString(), "ether")
      );
      this.CheDonationContract.methods
        .investIn(web3.utils.toWei(_this.stakeAmount2.toString(), "ether"))
        .send({
          from: account,
        })
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_CheDonationContract();
          _this.add_HOYCoinContract();
          _this.getCoinBalanceCHE();

          _this.stakeAmount2 = "";
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },

    max: function () {
      var num = web3.utils.fromWei(this.coinBalance, "ether");
      var prec = 3;
      var ceil = true;
      num = num.toString();
      let index = num.indexOf(".");
      if (index !== -1) {
        num = num.substring(0, 4 + index + 1);
      } else {
        num = num.substring(0);
      }

      this.stakeAmount = num;
    },
  },
  mounted() {
    var _this = this;
     this.lang = localStorage.getItem("lang") || "en";
    this.changeLanguage(localStorage.getItem("lang") || "en");

    var lastTime = Number(localStorage.getItem("lastTime"));
    var nowTime = Number(Date.now());
    console.log((nowTime - lastTime) / 1000);
    if (lastTime + 43200000 <= nowTime) {
      this.show = true;
    }

    var getData = () => {
      _this.add_HOYCoinContract();
      _this.add_HbyCoinContract();
      _this.add_HbyDonationContract();
      _this.add_CheDonationContract();
      _this.add_CheCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      _this.timer = setInterval(() => {
        if (!_this.HbyDonationInfo || !_this.HOYinfo || !_this.CheDonationInfo) {
          getData();
        } else {
          _this.getCoinBalance();
          _this.getCoinBalanceCHE();
          _this.isApproveFun();
          _this.isApproveFunCHE();
          clearInterval(_this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 
    _this.timer2 = setInterval(() => {
      if (_this.timer) {
        clearInterval(_this.timer);
        _this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

