<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";
import Web3 from "web3";
export default {
  data() {
    return {
      lang: localStorage.getItem("lang") || "en",
      tabsNum:'1',
      isHome:true,
      coinBalance: 0,
      coinBalance1: 0,
      coinBalance2: 0,
      coinBalance3: 0,
    };
  },
  computed: {
    ...mapState("main", [
      "CHEInfoPool",
      "account",
      "HOYinfo",
      "destroyHoyPool",
       "HTInfoPool",
        "LPInfo",
        "HTCheCoinContract",
        "UsdtCheCoinContract",
      "HbyCoinContract",
      "CheCoinContract"
    ]),
  },
  components: {
    HeadTop
  },
  methods: {
    ...mapActions("main", [
      "add_HTInfoPool",
      "add_CHEInfoPool",
      "add_HOYCoinContract",
      "add_destroyHoyContract",
      "add_LPCoinContract",
      "add_HTCheCoinContract",
      "add_UsdtCheCoinContract",
      "add_HbyCoinContract",
      "add_CheCoinContract"
    ]),
    changeTab:function(type){
        this.tabsNum = type;
    },
    protectAddress: function (addr, num) {
      if (addr) {
        var newAddr = addr.replace('"', "").replace('"', "");
        if (newAddr == "0x0000000000000000000000000000000000000000") {
          return "--";
        }
        if (!newAddr) {
          return "--";
        }
        if (newAddr == 1) {
          return newAddr;
        }
        return (newAddr + "").length == 42
          ? newAddr.substring(0, num) + "****" + newAddr.substring(39, 42)
          : newAddr;
      }
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    hreftwo(url) {
      console.log(url);
      this.$router.push({ path: url });
    },
   
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
        //HBY
    getCoinBalance: function () {
      if (this.HOYinfo) {
        var account = this.account;
        this.HTCheCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            console.log('coinBalance=================',res)
            this.coinBalance = res;
          });
      }
    },
        //HBY
    getCoinBalance1: function () {
      if (this.HOYinfo) {
        var account = this.account;
        this.UsdtCheCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            console.log('coinBalance1=================',res)
            this.coinBalance1 = res;
          });
      }
    },
        //HBY
    getCoinBalance2: function () {
      if (this.HOYinfo) {
        var account = this.account;
        this.HbyCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            console.log('coinBalance2=================',res)
            this.coinBalance2 = res;
          });
      }
    },
        //HBY
    getCoinBalance3: function () {
      if (this.HOYinfo) {
        var account = this.account;
        this.CheCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            console.log('coinBalance3=================',res)
            this.coinBalance3 = res;
          });
      }
    },
    
  },
  created() {
  },
  mounted() {
    var _this = this;
       this.lang = localStorage.getItem("lang") || "en";
    this.changeLanguage(localStorage.getItem("lang") || "en");

    var getData = () => {
      _this.add_CHEInfoPool();
      _this.add_HOYCoinContract();
       _this.add_destroyHoyContract();
       _this.add_HTInfoPool();
       _this.add_LPCoinContract();
         _this.add_HTCheCoinContract();
       _this.add_UsdtCheCoinContract();
       _this.add_HbyCoinContract();
       _this.add_CheCoinContract();
    };

   
    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!_this.CHEInfoPool || !_this.HOYinfo ) {
          getData();
     
        } else {
               _this.getCoinBalance3();
          _this.getCoinBalance2();
          _this.getCoinBalance1();
          _this.getCoinBalance();

          clearInterval(_this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

<style scoped  lang="less">
