import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/page/home/home'
import Donation from '@/page/home/donation'
import Ming_usdt from '@/page/home/ming_usdt'
import Ming_ht from '@/page/home/ming_ht'
import Destroy from '@/page/home/destroy'
import HoyDestroy from '@/page/home/hoyDestroy'


Vue.use(Router);


//push
const originalPush = Router.prototype.push
    //push
Router.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}

export default new Router({
    routes: [
        {
            path: '/',
            name: 'index',
            component: Home
        },
        {
            path: '/ming_usdt',
            name: 'ming_usdt',
            component: Ming_usdt
        },
        {
            path: '/ming_ht',
            name: 'ming_ht',
            component: Ming_ht
        },
        {
            path: '/destroy',
            name: 'destroy',
            component: Destroy
        },
        {
            path: '/donation',
            name: 'donation',
            component: Donation
        },{
            path: '/hoyDestroy',
            name: 'hoyDestroy',
            component: HoyDestroy
        },
    ]
})


