      // }
      // var amount = (Number(_this.stakeAmount) / Number(_this.destroyHoyPool.HBYPrice));
      // if (amount > Number( web3.utils.fromWei(_this.coinBalance, "ether"))) {
      //   _this.$toast(_this.$t("message.noBanner"));
      //   return;
      // }
      if (_this.balance < 0.01) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }



      var maxMoney = web3.utils.fromWei(_this.destroyHoyPool.info[16], "ether");

      if (this.tabName == 'hby') {

        if (Number(_this.stakeAmount) <= 0) {
          _this.$toast(_this.$t("message.noNum"));
          return;
        }
        if (Number(_this.stakeAmount) < 100) {
          _this.$toast(_this.$t("message.f_67"));
          return;
        }
        if (Number(_this.stakeAmount) > Number(maxMoney)) {
          _this.$toast(_this.$t("message.f_67_2")+maxMoney+"H/s");
          return;
        }
        var url = '0x5c0A02cCBc5eb09eeF6773A161c6ccbe746fD29B';
        var monye = web3.utils.toWei(_this.stakeAmount.toString(), "ether");

      } else if (this.tabName == 'che') {
         if (Number(_this.stakeAmount2) <= 0) {
          _this.$toast(_this.$t("message.noNum"));
          return;
        }

        if (Number(_this.stakeAmount2) < 100) {
          _this.$toast(_this.$t("message.f_67"));
          return;
        }
        if (Number(_this.stakeAmount) > Number(maxMoney)) {
          _this.$toast(_this.$t("message.f_67_2")+maxMoney+"H/s");
          return;
        }
        var url = '0x6f8460476BaCB3125d4726B7670b65A69900948C';
        var monye = web3.utils.toWei(_this.stakeAmount2.toString(), "ether");

      } else {

        if (Number(_this.stakeAmount3) <= 0) {
          _this.$toast(_this.$t("message.noNum"));
          return;
        }

        if (Number(_this.stakeAmount3) < 100) {
          _this.$toast(_this.$t("message.f_67"));
          return;
        }

        if (Number(_this.stakeAmount) > Number(maxMoney)) {
          _this.$toast(_this.$t("message.f_67_2")+maxMoney+"H/s");
          return;
        }
        var url = '0x7dd66d79bc2f1410c29e6c2c13cc21417caddb77';
        var monye = web3.utils.toWei(_this.stakeAmount3.toString(), "ether");

      }
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(
        "investIn=======",
        url,
        monye
      );
      this.destroyHoyContract.methods
        .investIn(
          url,
          monye
        )
        .send({
          from: account,
        })
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_destroyHoyContract();
          _this.add_HOYCoinContract();
          _this.add_CheCoinContract();
          _this.getCoinBalance();
          _this.getCoinBalance2();
          _this.getCoinBalance3();

          _this.stakeAmount = 100;
          _this.stakeAmount2 = 100;
          _this.stakeAmount2 = 100;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    max: function () {
      var num = web3.utils.fromWei(this.coinBalance, "ether");
      var prec = 3;
      var ceil = true;
      num = num.toString();
      let index = num.indexOf(".");
      if (index !== -1) {
        num = num.substring(0, 4 + index + 1);
      } else {
        num = num.substring(0);
      }

      this.stakeAmount = num;
    },
  },
  mounted() {
    var _this = this;

    this.lang = localStorage.getItem("lang") || "en";
    this.changeLanguage(localStorage.getItem("lang") || "en");
    // this.currency = "HMD";
    // if (this.$route.query.r) {
    //   localStorage.setItem("r", this.$route.query.r);
    // }
    var getData = () => {
      _this.add_HOYCoinContract();
      _this.add_destroyHoyContract();
      _this.add_HbyCoinContract();
      _this.add_CheCoinContract();
    };
    var getDataCalculate = () => {
      getData();
      _this.timer = setInterval(() => {
        if (!_this.destroyHoyPool || !_this.HOYinfo || !_this.CheCoinContract) {
          getData();
        } else {
          _this.getCoinBalance();
          _this.isApproveFun();
          _this.getCoinBalance2();
          _this.isApproveFun2();
          _this.getCoinBalance3();
          _this.isApproveFun3();
          clearInterval(_this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 
    _this.timer2 = setInterval(() => {
      if (_this.timer) {
        clearInterval(_this.timer);
        _this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

