// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueI18n from 'vue-i18n'
import * as filters from './filters/filters.js'


import LangEn from '../static/lang/en'
import LangZhCHS from '../static/lang/zhCHS'
import LangZhCHT from '../static/lang/zhCHT'


import store from "./store";

import Vant from 'vant';
import 'vant/lib/index.css';
// import './assets/all.css';

  


import VueClipboard from 'vue-clipboard2';

Vue.use(VueClipboard)
Vue.use(Vant);
Vue.use(Vant);
Vue.prototype.ArrList={
    cheUSDTArr:"0x040b806829bc644785db08107a33d135332cb289",
    cheHTArr:"0x7b61a848996d576553925440de6c2e44991c8578",
    UsdtArr:"0xa71EdC38d189767582C38A3145b5873052c3e47a",
    cheArr:"0x6f8460476BaCB3125d4726B7670b65A69900948C",
    HBYArr:"0x5c0A02cCBc5eb09eeF6773A161c6ccbe746fD29B",
    hoyArr:"0x7dd66d79bc2f1410c29e6c2c13cc21417caddb77",
    XhArr:"0x000000000000000000000000000000000000dead",
    xhDappArr:"0x77d841D23560fdB65a9eD27de484F5f97a447a9b",
    HBYDappArr:"0x3616d224c49a4a79e013106b60ff053d61b5b886",
    CHEDappArr:"0x6973b93d17efcd1aadf0aff6bbc7ce8d9154ba76",
}

Vue.config.productionTip = false;

Object.keys(filters).forEach(key=>{
    Vue.filter(key,filters[key])//
})

Vue.use(VueI18n);



const i18n = new VueI18n({
    locale: 'zhCHS', // 
    messages: {
        'en': LangEn,
        'zhCHS': LangZhCHS,
        'zhCHT': LangZhCHT
    }
});



/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    i18n,
    template: '<App/>',
    components: { App }
});


