          <van-col span="12" class="textRight">
            <a class="btn"  href="https://ht.mdex.co/#/swap?lang=zh-CN"> {{ $t("message.f_41") }} </a>
          </van-col>
          <van-col span="12">
           <a class="btn"  href="https://ht.mdex.co/#/swap?lang=zh-CN">  {{ $t("message.f_42") }}  </a>
          </van-col>
        </van-row> -->
        <div class="h"></div>
        <h2 style="margin-top:20px"> {{ $t("message.f_43") }} </h2>
        <div class="sy">
          <img src="../../assets/img/hoy/min-logo.png" alt="" width="30">
         {{destroyHoyPool2 ? (destroyHoyPool2.info[8]) : "0"  | figureK  }} HOY
        </div>
        <h3>  {{destroyHoyPool2 ? (Number(destroyHoyPool2.info[8])*(Number(destroyHoyPool2.HOYPrice))) : "0"  | figureK  }} H/s</h3>
        <div class="h"></div>
        <van-row gutter="30" type="flex" justify="center" >
          <van-col span="12" class="textCenter" >
            <a
              class="btn"
             @click="getReward"
            >
              {{ $t("message.f_31") }}</a
            >
          </van-col>
          <van-col span="12">
            <a
              class="btn"
             @click="destroyHoy"
            >
              {{ $t("message.f_18") }}HOY</a
            >
          </van-col>
        </van-row>
        <p>{{ $t("message.f_44") }}</p>
    </div>


    <div class="cent">
      <div class="centTop" >
        <div class="imgBox"><img
                src="../../assets/img/hoy/min-logo.png"
                alt=""
                width="60"
              />
        </div>
        <div class="txtBox">
          <h1>{{ $t("message.f_45") }}</h1>
          <p>{{ $t("message.f_45_1") }}</p>
        </div>
      </div>
      <div class="putBox" >
        <div>
          <p>{{ $t("message.f_46_1") }}(H/s)</p>
          <!-- onkeyup="this.value=this.value.replace(/[^0-9]/g,'')" -->
         <input type="number" placeholder="0" v-model="stakeAmount" onkeyup="this.value=this.value.replace(/[^0-9]/g,'')"/>
        </div>
        <div class="textCenter" style="margin:20px 0 10px">
          <img src="../../assets/img/hoy/dh.png" alt="" width="35">
        </div>
        
        <div>
          <p>{{ $t("message.f_18") }}HBY <span>{{ $t("message.f_8") }}{{coinBalance | figureK}}</span></p>
          <div class="xhBox">{{destroyHoyPool2? (Number(stakeAmount) / Number(destroyHoyPool2.HOYPrice)) : "0" | price}}</div>
         <!-- <input type="number" placeholder="0"  v-model="stakeAmount" disabled="disabled"/> -->
        </div>
         <button class="btn" @click="stake" v-if="isApprove">{{ $t("message.f_47") }}</button>
          <button class="btn" @click="lpApprove" v-else>{{ $t("message.f_10_1") }}</button>
         <h4> {{ $t("message.f_48") }}</h4>
      </div>
      <div class="list">
        <ul>
          <li>{{ $t("message.f_49") }}<span>{{destroyHoyPool2 ? destroyHoyPool2.info[0] : "0"  | figureK  }} H/s</span></li>
          <li>{{ $t("message.f_51_1") }}<span>{{destroyHoyPool2 ? (destroyHoyPool2.info[15]) : "0"  | figureK  }} HOY</span></li>
          <li>{{ $t("message.f_a_1") }}<span>{{destroyHoyPool2 ? (destroyHoyPool2.info[13]) : "0"  | figureK  }} H/s</span></li>
          <li>{{ $t("message.f_a_2") }}<span>{{destroyHoyPool2 ? (destroyHoyPool2.info[7]) : "0"  | figureK  }} HOY</span></li>
          <!-- <li>HOY<span>{{destroyHoyPool2 ? (destroyHoyPool2.info[10]) : "0"  | figureK  }} USDT</span></li> -->
          <!-- <li><span>{{destroyHoyPool2 ? (destroyHoyPool2.info[12]) : "0"  | figureK  }} FLW</span></li> -->
          <li>HOY{{ $t("message.f_36") }}<span>{{ protectAddress('0x7dd66d79bc2f1410c29e6c2c13cc21417caddb77', 4) }}</span></li>
        </ul>
          <!-- <van-row gutter="30" type="flex" justify="space-between">
          <van-col span="12" class="textRight">
            <a
              class="btn"
              href="https://hecoinfo.com/address/0x5c0A02cCBc5eb09eeF6773A161c6ccbe746fD29B"
            >
              {{ $t("message.f_a_3") }}</a
            >
          </van-col>
          <van-col span="12" class="textRight">
            <a
              class="btn"
              href="https://hecoinfo.com/address/0x6f8460476BaCB3125d4726B7670b65A69900948C"
            >
              {{ $t("message.f_a_4") }}</a
            >
          </van-col>
        </van-row> -->
      </div>
    </div>
