<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: localStorage.getItem("lang") || "zhCHS",
      link: "--",
        coinBalance: 0,
        isApprove: false,
        stakeAmount: 100,
         stakeAmount2: 100,
        tabName: 'hby',
      timer: null,
      timer2: null,
      TokenPrice: 0,
      isApprove2:false,
    };
  },
  computed: {
       ...mapState("main", [
      "destroyHoyPool2",
      "account",
      "HOYinfo",
      "HOYCoinContract",
      //  "CheCoinContract",
      "destroyHoyContract2"
    ]),
  },
  methods: {
    ...mapActions("main", [
      // "add_CheCoinContract",
      "add_HOYCoinContract",
      // "add_HOYCoinContract",
      "add_destroyHoyContract2"
    ]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    changTab(name){
      this.tabName = name;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
   destroyHoy: function () {
      if (this.destroyHoyContract2 == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.destroyHoyContract2.methods
        .destroyHoy()
        .send({ from: account })
        .then((res) => {
          console.log("destroyHoy", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
         _this.add_destroyHoyContract2();
           _this.add_HOYCoinContract();
          _this.getCoinBalance();
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getReward: function () {
      if (this.destroyHoyContract2 == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.destroyHoyContract2.methods
        .getReward()
        .send({ from: account })
        .then((res) => {
          console.log("getReward", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
         _this.add_destroyHoyContract2();
           _this.add_HOYCoinContract();
          _this.getCoinBalance();
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },

        //HBY
    getCoinBalance: function () {
      if (this.HOYCoinContract) {
        var account = this.account;
        this.HOYCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            this.coinBalance = res;
          });
      }
    },
        lpApprove: function () {
      if (this.HOYCoinContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.destroyHoyPool2.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.HOYCoinContract.methods
        .approve(address, "5000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
     // 
    isApproveFun: function () {
      var account = this.account;
      var address = this.destroyHoyPool2.address;

      this.HOYCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log("usdt=========isApproveFun", account, address);
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
            lpApprove2: function () {
      if (this.CheCoinContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.destroyHoyPool2.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.CheCoinContract.methods
        .approve(address, "5000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove2 = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
     // 
    // isApproveFun2: function () {
    //   var account = this.account;
    //   var address = this.destroyHoyPool2.address;

    //   this.CheCoinContract.methods
    //     .allowance(account, address)
    //     .call()
    //     .then((res) => {
    //       console.log("usdt=========isApproveFun", account, address);
    //       var a = web3.utils.fromWei(res, "ether");
    //       if (a > 0) {
    //         this.isApprove2 = true;
    //       }
    //     });
    // },
      stake: function () {
      if (this.destroyHoyPool2== null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;

      if (!_this.isApprove) {
        _this.$toast(_this.$t("message.noAccredit"));
        return;
      }
      // var amount = (Number(_this.stakeAmount) / Number(_this.destroyHoyPool2.HOYPrice));
      // if (amount > Number( web3.utils.fromWei(_this.coinBalance, "ether"))) {
      //   _this.$toast(_this.$t("message.noBanner"));
      //   return;
      // }
      if (_this.balance < 0.01) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      if (Number(_this.stakeAmount) < 100) {
        _this.$toast(_this.$t("message.f_67"));
        return;
      }
      if (Number(_this.stakeAmount) <= 0) {
        _this.$toast(_this.$t("message.noNum"));
        return;
      }
      var monye = (_this.stakeAmount * 10 ** 18).toString();
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      var url = '0x7dd66d79bc2f1410c29e6c2c13cc21417caddb77';
      var monye =  web3.utils.toWei(_this.stakeAmount2.toString(), "ether");
      console.log(
        "investIn=======",
       url,
        monye
      );
      this.destroyHoyContract2.methods
        .investIn(
         url,
         monye
        )
        .send({
          from: account,
        })
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_destroyHoyContract2();
           _this.add_HOYCoinContract();
          _this.getCoinBalance();

          _this.stakeAmount = 0;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    max: function () {
      var num = web3.utils.fromWei(this.coinBalance, "ether");
      var prec = 3;
      var ceil = true;
      num = num.toString();
      let index = num.indexOf(".");
      if (index !== -1) {
        num = num.substring(0, 4 + index + 1);
      } else {
        num = num.substring(0);
      }

      this.stakeAmount = num;
    },
  },
  mounted() {
    var _this = this;
    
    this.lang = localStorage.getItem("lang") || "zhCHS";
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    // this.currency = "HMD";
    // if (this.$route.query.r) {
    //   localStorage.setItem("r", this.$route.query.r);
    // }
    var getData = () => {
      _this.add_HOYCoinContract();
      _this.add_destroyHoyContract2();
      // _this.add_HOYCoinContract();
      // _this.add_CheCoinContract();
    };
    var getDataCalculate = () => {
      getData();
      _this.timer = setInterval(() => {
        if (!_this.destroyHoyPool2 || !_this.HOYCoinContract ||!_this.destroyHoyContract2) {
          getData();
        } else {
         _this.getCoinBalance();
         _this.isApproveFun();
        //  _this.getCoinBalance2();
        //  _this.isApproveFun2();
          clearInterval(_this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 
    _this.timer2 = setInterval(() => {
      if (_this.timer) {
        clearInterval(_this.timer);
        _this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

