<template>
  <div class="info">
    <HeadTop :lang="lang" @changeAppLocale="changeAppLocale" :titleHead="this.$t('message.f_38')"/>

     <div class="arrd">
        <van-row>
          <van-col span="12" class="textCenter">
            <div class="bgWhite flex">
              <div class="bg_logo">
                <img src="../../assets/img/hoy/topLogo.png" alt="" />
                <span>HECO</span>
              </div>
              <p>{{ protectAddress(this.account, 4) }}</p>
            </div>
          </van-col>
          <van-col span="12" class="textCenter">
            <div class="bgWhite flex">
              <img src="../../assets/img/hoy/min-logo.png" alt="" width="25" />
              <p class="textRight">{{ HOYinfo ? HOYinfo.balance : "0" | figureK }} HOY</p>
            </div>
          </van-col>
        </van-row>
      </div>

    <div class="swipeBox">
       <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white" :show-indicators="false">
        <van-swipe-item><img :src="require('../../assets/img/hoy/banner_3_'+ lang +'.png')" alt="" width="100%"></van-swipe-item>
        <van-swipe-item><img :src="require('../../assets/img/hoy/banner_4_'+ lang +'.png')" alt="" width="100%"></van-swipe-item>
        </van-swipe>
    </div>

          <div class="row-list">
        <van-row gutter="30" type="flex" justify="space-between">
          <van-col span="12" class="textRight">
            <a
              class="lBtn"
              href="https://ht.mdex.co/#/swap?lang=zh-CN"
            >
             {{ $t("message.f_29_3") }}</a
            >
          </van-col>
          <van-col span="12">
            <a
              class="rBtn"
              href="https://ht.mdex.com/#/add/HT/0x6f8460476BaCB3125d4726B7670b65A69900948C"
            >
               {{ $t("message.f_29_4") }}</a
            >
          </van-col>
        </van-row>
      </div>     

      <div class="top">
        <h2> {{ $t("message.f_30") }}</h2>
        <div class="sy">
          <img src="../../assets/img/hoy/min-logo.png" alt="" width="30">
          {{CHEInfoPool ? CHEInfoPool.info[8]:'0' | figureK4}} HOY
        </div>
        <h3> {{CHEInfoPool ? (Number(CHEInfoPool.info[8])*Number(CHEInfoPool.HOYPrice)):'0' | figureK4}}  USDT</h3>
        <div class="h"></div>
        <van-row gutter="30" type="flex" justify="space-between">
          <van-col span="24" class="textRight">
            <a
              class="btn" @click="getReward" >
              {{ $t("message.f_31") }}</a
            >
          </van-col>
          <!-- <van-col span="12">
            <a
              class="btn"
              href="https://pancakeswap.finance/swap?inputCurrency=0x55d398326f99059ff775485246999027b3197955&outputCurrency=0x3d5821dbcb4c7ecc7b06b8016468e98180e8b638"
            >
              HOY</a
            >
          </van-col> -->
        </van-row>
        <!-- <p>TIpsHOY</p> -->
    </div>


    <div class="cent">
      <div class="centTop">
        <div class="imgBox">
           <img
                src="../../assets/img/hoy/usdt.png"
                alt=""
                width="55"
              /><img
                src="../../assets/img/hoy/che.png"
                alt=""
                width="55"
                style="margin-left: -25px"
              />
        </div>
        <div class="txtBox">
          <h1>USDT-CHE/LP</h1>
          <p>{{ $t("message.f_13") }}</p>
        </div>
      </div>
      <div class="putBox">
        <p>LP{{ $t("message.f_8") }}{{coinBalance}}  {{usdtCheInfo ? (Number(coinBalance)/Number(usdtCheInfo.totalSupply)*Number(usdtCheInfo.liquidity)*2):'0' | figureK4}} USDT <span @click="max">MAX</span></p>
         <input type="number" placeholder="0"  v-model="stakeAmount"/>
         <button class="btn" @click="stake" v-if="isApprove">{{ $t("message.f_32") }}</button>
          <button class="btn" @click="lpApprove" v-else>{{ $t("message.f_10_1") }}</button>
      </div>
      <div class="list">
        <ul>
          <li>{{ $t("message.f_33") }}<span>{{CHEInfoPool ? CHEInfoPool.info[0]:'0' | figureK4}} LP</span></li>
          <li>{{ $t("message.f_34") }}<span>{{CHEInfoPool ? CHEInfoPool.info[6]:'0' | figureK4}} LP <span v-if="usdtCheInfo"> {{CHEInfoPool ? (Number(CHEInfoPool.info[6])/Number(usdtCheInfo.totalSupply)*Number(usdtCheInfo.liquidity)*2):'0' | figureK4}} USDT</span> </span></li>
          <li>{{ $t("message.f_35") }}<span>{{CHEInfoPool ? CHEInfoPool.info[7]:'0' | figureK4}} HOY</span></li>
          <!-- <li><span>{{CHEInfoPool ? CHEInfoPool.info[6]:'0' | figureK}} LP</span></li> -->
          <!-- <li><span>6FLW</span></li> -->
          <li>CHE{{ $t("message.f_36") }}<span>{{ protectAddress('0x6f8460476BaCB3125d4726B7670b65A69900948C', 4) }}</span></li>
          <!-- <li><span>{{hmdInfo ? (hmdInfo.info[12]) : "0"  | figureK  }} FLW</span></li> -->
        </ul>
          <van-row gutter="30" type="flex" justify="space-between">
          <van-col span="24" class="textRight">
            <a
              class="btn"
              @click="exitFun">
              {{ $t("message.f_37") }}LP</a
            >
          </van-col>
          <!-- <van-col span="12">
            <a
              class="btn"
              :href="'https://hecoinfo.com/address/'+(CHEInfoPool ? CHEInfoPool.address : '')"
            >POS</a>
          </van-col> -->
        </van-row>
      </div>
    </div>
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: localStorage.getItem("lang") || "en",
      link: "--",
        coinBalance: 0,
        isApprove: false,
        stakeAmount:0,
      //   withdrawAmount: 0,
      timer: null,
      timer2: null,
      TokenPrice: 0,
    };
  },
  computed: {
       ...mapState("main", [
      "CHEInfoPool",
      "account",
      "HOYinfo",
      "UsdtCheCoinContract",
      "CHEInfoPoolContract",
      // "destroyHoyContract",
      "usdtCheInfo"
    ]),
  },
  methods: {
    ...mapActions("main", [
      "add_CHEInfoPool",
      "add_HOYCoinContract",
      "add_UsdtCheCoinContract",
      // "add_destroyHoyContract"
    ]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
     exitFun: function () {
      if (this.CHEInfoPoolContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.CHEInfoPoolContract.methods
        .exit()
        .send({ from: account })
        .then((res) => {
          console.log("exit", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
            _this.add_CHEInfoPool();
           _this.add_HOYCoinContract();
          _this.getCoinBalance();
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getReward: function () {
      if (this.CHEInfoPoolContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.CHEInfoPoolContract.methods
        .getReward()
        .send({ from: account })
        .then((res) => {
          console.log("getReward", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
         _this.add_CHEInfoPool();
           _this.add_HOYCoinContract();
          _this.getCoinBalance();
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },

        //
    getCoinBalance: function () {
      if (this.UsdtCheCoinContract) {
        var account = this.account;
        this.UsdtCheCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 5 + index + 1);
            } else {
              num = num.substring(0);
            }
              console.log('coinBalance======',num)
            this.coinBalance = num;
          });
      }
    },
        lpApprove: function () {
      if (this.UsdtCheCoinContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.CHEInfoPool.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.UsdtCheCoinContract.methods
        .approve(address, "5000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
     // 
    isApproveFun: function () {
      var account = this.account;
      var address = this.CHEInfoPool.address;

      this.UsdtCheCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log("usdt=========isApproveFun", account, address);
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
      stake: function () {
      if (this.CHEInfoPool == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;

      if (!_this.isApprove) {
        _this.$toast(_this.$t("message.noAccredit"));
        return;
      }
      if (Number(_this.stakeAmount) > Number(_this.coinBalance)) {
        _this.$toast(_this.$t("message.noBanner"));
        return;
      }
      if (_this.balance < 0.01) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      if (Number(_this.stakeAmount) <= 0) {
        _this.$toast(_this.$t("message.noNum"));
        return;
      }
      var monye = (_this.stakeAmount * 10 ** 18).toString();
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(
        "investIn=======",
        web3.utils.toWei(_this.stakeAmount.toString(), "ether")
      );
      this.CHEInfoPoolContract.methods
        .investIn(
          web3.utils.toWei(_this.stakeAmount.toString(), "ether")
        )
        .send({
          from: account,
        })
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          _this.add_CHEInfoPool();
           _this.add_HOYCoinContract();
          _this.getCoinBalance();

          _this.stakeAmount = "";
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    max: function () {
      // var num = web3.utils.fromWei(this.coinBalance, "ether");
      // var prec = 3;
      // var ceil = true;
      // num = num.toString();
      // let index = num.indexOf(".");
      // if (index !== -1) {
      //   num = num.substring(0, 2 + index + 1);
      // } else {
      //   num = num.substring(0);
      // }

      this.stakeAmount = this.coinBalance;
    },
  },
  mounted() {
    var _this = this;
        this.lang = localStorage.getItem("lang") || "en";
    this.changeLanguage(localStorage.getItem("lang") || "en");
    
    var getData = () => {
      _this.add_CHEInfoPool();
      _this.add_HOYCoinContract();
      _this.add_UsdtCheCoinContract();
      // _this.add_destroyHoyContract();
    };
    var getDataCalculate = () => {
      getData();
      _this.timer = setInterval(() => {
        if (!_this.CHEInfoPool || !_this.HOYinfo || !_this.UsdtCheCoinContract) {
          getData();
        } else {
         _this.getCoinBalance();
         _this.isApproveFun();
          clearInterval(_this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 
    _this.timer2 = setInterval(() => {
      if (_this.timer) {
        clearInterval(_this.timer);
        _this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.info {
background: linear-gradient(#2a1a11,#000,#000);
  padding: 5.5em 0 3em;
  min-height: 100%;
    box-sizing: border-box;
  .top {
    text-align: center;
    padding: 21px;
    width:92%;
    // height: 188px;
    box-sizing: border-box;
    background: url(../../assets/img/hoy/top_bg.png) top center no-repeat #542d13;
    border-radius: 10px;
    background-size: 100%;
    margin: 30px auto 10px;
    box-sizing: border-box;
     .h{
      height: 1px;
      background: #3c2008;
      border-bottom: 1px solid #5f3418;
      margin-bottom: 5px;
    }
    h3{
      font-size: 16px;
      text-align: center;
      font-weight: normal;
      color: #E2CA30;
      text-align: left;
      padding-left: 50px;
      margin-bottom: 20px;
    }
    .sy{
      font-size: 26px;
      text-align: left;
      display: flex;
      align-items: center;
      margin: 15px 0 10px;
      img{margin-right: 12px;}
      span{margin-left: 10px;}
    }
    h2 {
      font-size: 13px;
      font-family: DINAlternate-Bold, DINAlternate;
      font-weight: bold;
      text-align: center;
      color: #EF8701;
      text-align: left;
    }
    p{color: #FF4F26;}
  }




  .cent {
    background: #2F190B;
    // padding: 20px;
    width: 92%;
    margin: 50px auto 30px;
    position: relative;
    box-sizing: border-box;
    border-radius: 15px;
   .centTop{
      background: url(../../assets/img/hoy/right3.png) top center no-repeat;
      background-size: 100%;
      padding: 20px 0 20px 20px;
      display: flex;
      align-items: center;
      .txtBox{
        margin-left: 5px;
        color: #E2CA30;
        h1{
          font-size: 20px;
          margin-bottom: 5px;
        }
      }
   }
   .putBox{
     padding: 25px 20px 5px;
     background: #542D13;
     border-radius: 0 0 10px 10px;
     p{color: #EEBB1E;
      span{float: right;font-size: 16px;text-decoration: underline;color: #EEBB1E;}
     }
     input{
       width: 100%;
       background: #3A1F0D;
       border: 1px solid #6E3B19;
       border-radius: 10px;
       padding: 12px 15px;
       box-sizing: border-box;
       font-size: 20px;
       margin: 15px 0 0;
     }
   }
    
 .list{
   padding: 20px;
 }
    li{
      position: relative;
      // margin-left: 20px;
      padding: 15px 0;
      border-bottom:1px solid #000000;
      color: #B66701;
          display: flex;
    align-items: center;
    justify-content: space-between;
      span{font-size: 15px;
        // float: right;
        display: block;
        color: #EEBB1E;
        width: 60%;
        text-align: right;
       overflow: hidden; /**/
      white-space: nowrap; /**/
      text-overflow: ellipsis;
      span{
        width: 100%;
        margin-top: 8px;
      }
        
      }
    }
    .btn{
      margin: 30px auto 20px;
      display: block;
      width: 145px;
      height: 48px;
 
    }
  }
}
.swipeBox{
  width: 92%;
  margin: 20px auto;
  .my-swipe{width: 100%;}
}
  .row-list {
    margin: 0 10px;
    .sj,
    .sj2 {
      padding: 10px 10px 12px;
      p {
        color: #74262a;
      }
      span {
        font-size: 18px;
        font-family: DINPro;
        font-weight: bold;
        margin-top: 5px;
        display: inline-block;
        color: #3d2329;
      }
    }
    a.lBtn,
a.rBtn {
  display: inline-block;
  color: #3d2329;
  background: url(../../assets/img/hoy/btn.png) top center no-repeat;
  background-size: 100%;
  width: 160px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 16px;
  border: none;
  font-weight: bold;
}
  }
.btn {
  background: url(../../assets/img/hoy/44414141.png) top center no-repeat;
  background-size: 100%;
  border: none;
  display: block;
  // width: 120px;
  text-align: center;
  font-size: 16px;
  padding: 3px 0;
  // font-weight: bold;
  height: 48px;
  line-height: 42px;
  margin: 18px auto;
  color: #fff;
  min-width: 140px;
  max-width: 160px;
}
.arrd {
  margin-top: 0px;
  .bgWhite {
    width: 90%;
    // height: 60px;
    margin: 0px auto 0;
    padding: 4px;
    background: #89272b;
    border: 1px solid #ffb050;
    opacity: 0.8;
    border-radius: 10px;
    p {
      margin-left: 10px;
      height: 25px;
      line-height: 25px;
    }
  }
  .flex {
    display: flex;
  }
  .bg_logo {
    height: 25px;
    line-height: 25px;
    font-size: 10px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: normal;
    color: #fff;
    background: #ffb151;
    border-radius: 4px;
    border-radius: 10px;
    padding: 4px;
    padding: 0 4px;
    img {
      width: 12px;
      vertical-align: sub;
    }
  }
}
</style>



