import { setStorage, getStorage } from '@/assets/js/units.js';
import getWeb3 from '@/assets/js/getWeb3.js'
import pool_DOT from '@/assets/pool_abi/pool_DOT.json'
import LPABI from '@/assets/coin_abi/LP.json'
import HMDABI from '@/assets/coin_abi/HMD.json'
import DOT from '@/assets/coin_abi/DOT.json'
import pool_other from '@/assets/pool_abi/pool_other.json'
import pool_LP from '@/assets/pool_abi/pool_LP.json'
import coin_lp from '@/assets/pool_abi/pool_LP_1.json'
import deposit_pool from '@/assets/pool_abi/deposit_pool.json'
import deposit_pool2 from '@/assets/pool_abi/deposit_pool2.json'
import Vue from 'vue'


const state = {
  /**/
  account: JSON.parse(getStorage('account') || 0),
  balance: JSON.parse(getStorage('balance') || 0),
  HOYinfo: JSON.parse(getStorage('HOYinfo') || 0),
  CheCoinContract: JSON.parse(getStorage('CheCoinContract') || null),
  destroyHoyPool: JSON.parse(getStorage('destroyHoyPool') || null),
  destroyHoyPool2: JSON.parse(getStorage('destroyHoyPool2') || null),
   HbyCoinContract: JSON.parse(getStorage('HbyCoinContract') || null),
  CHEInfoPoolContract: JSON.parse(getStorage('CHEInfoPoolContract') || null),//che
  CHEInfoPool: JSON.parse(getStorage('CHEInfoPool') || null),
  HTInfoPoolContract: JSON.parse(getStorage('HTInfoPoolContract') || null),//che
  HTInfoPool: JSON.parse(getStorage('HTInfoPool') || null),
  HOYCoinContract: JSON.parse(getStorage('HOYCoinContract') || null),//hoy
  UsdtCheCoinContract: JSON.parse(getStorage('UsdtCheCoinContract') || null),//usdt+che
  HTCheCoinContract: JSON.parse(getStorage('HTCheCoinContract') || null),//HT+che
  destroyHoyContract:JSON.parse(getStorage('destroyHoyContract') || null),
  destroyHoyContract2:JSON.parse(getStorage('destroyHoyContract2') || null),
  usdtCheInfo:JSON.parse(getStorage('usdtCheInfo') || null),
  HTCheInfo:JSON.parse(getStorage('HTCheInfo') || null),
  HbyDonationContract:JSON.parse(getStorage('HbyDonationContract') || null),
  HbyDonationInfo:JSON.parse(getStorage('HbyDonationInfo') || null),
  CheDonationContract:JSON.parse(getStorage('CheDonationContract') || null),
  CheDonationInfo:JSON.parse(getStorage('CheDonationInfo') || null),
  LPInfo:JSON.parse(getStorage('LPInfo') || null),
}

const getters = {
  /**/
  getIsApprove: function (state) {
    let isApprove = null;
    if (state.isApprove) {
      isApprove = state.isApprove;
    } else {
      isApprove = JSON.parse(getStorage('isApprove') || null);
    }
    return isApprove
  },

}

const mutations = {

  set_account: (state, data) => {
    state.account = data;
  },
 
  set_balance: (state, data) => {
    state.balance = data;
  },
  set_UsdtCheCoinContract: (state, data) => {
    state.UsdtCheCoinContract = data;
  },
  set_HTCheCoinContract: (state, data) => {
    state.HTCheCoinContract = data;
  },
  // HBY
  set_HbyCoinContract: (state, data) => {
    state.HbyCoinContract = data;
  },
 
  // usdt
  set_CHEInfoPool: (state, data) => {
    state.CHEInfoPool = data;
  },
  set_CHEInfoPoolContract: (state, data) => {
    state.CHEInfoPoolContract = data;
  },
  // HT
  set_HTInfoPool: (state, data) => {
    state.HTInfoPool = data;
  },
  set_HTInfoPoolContract: (state, data) => {
    state.HTInfoPoolContract = data;
  },
  // // 
  set_destroyHoyContract: (state, data) => {
    state.destroyHoyContract = data;
  },
    // // 
    set_destroyHoyContract2: (state, data) => {
      state.destroyHoyContract2 = data;
    },
  set_HOYinfo:(state, data) => {
    state.HOYinfo = data;
  },
  set_HOYCoinContract:(state, data) => {
    state.HOYCoinContract = data;
  },
  // 
  set_destroyHoyPool:(state, data) => {
    state.destroyHoyPool = data;
  },
  // 
  set_destroyHoyPool2:(state, data) => {
    state.destroyHoyPool2 = data;
  },
    // che
    set_CheCoinContract:(state, data) => {
    state.CheCoinContract = data;
  },
      // usdt+che lp
      set_usdtCheInfo:(state, data) => {
        state.usdtCheInfo = data;
      },
            // usdt+che lp
            set_HTCheInfo:(state, data) => {
              state.HTCheInfo = data;
            },
        // 
        set_HbyDonationContract:(state, data) => {
          state.HbyDonationContract = data;
        },
        set_HbyDonationInfo:(state, data) => {
          state.HbyDonationInfo = data;
        },
         // che
         set_CheDonationContract:(state, data) => {
          state.CheDonationContract = data;
        },
        set_CheDonationInfo:(state, data) => {
          state.CheDonationInfo = data;
        },
        set_LPInfo:(state, data) => {
          state.LPInfo = data;
        },
  
}

const actions = {

  // 1che+usdt
  add_CHEInfoPool: ({ commit }, data) => {
    var _this = this;
    let info = {};
    getWeb3.Init(pool_other,  Vue.prototype.ArrList.cheUSDTArr, (contract, addr, balance) => {
      commit('set_CHEInfoPoolContract', contract);
      commit('set_account', addr);
      commit('set_balance', web3.utils.fromWei(balance, 'ether'));
      info.address =  Vue.prototype.ArrList.cheUSDTArr;
      contract.methods.getMiningInfo(addr).call().then((res) => {
        
        info.info = res.ct;
        info.earned =web3.utils.fromWei(res.ct[8], 'ether');
      }).then(()=>{
        contract.methods.getTokenPrice(Vue.prototype.ArrList.hoyArr,Vue.prototype.ArrList.UsdtArr,'1000000000000000000').call().then((res2) => {
          info.HOYPrice = web3.utils.fromWei(res2, 'ether');
          console.log(info)
          commit('set_CHEInfoPool',info )
        })
      })

    })
  },

    // 1che-ht
    add_HTInfoPool: ({ commit }, data) => {
      var _this = this;
      let info = {};
      getWeb3.Init(pool_DOT, Vue.prototype.ArrList.cheHTArr, (contract, addr, balance) => {
        commit('set_HTInfoPoolContract', contract);
        commit('set_account', addr);
        commit('set_balance', web3.utils.fromWei(balance, 'ether'));
        info.address = Vue.prototype.ArrList.cheHTArr;
        contract.methods.getMiningInfo(addr).call().then((res) => {
          
          info.info = res.ct;
          info.earned =web3.utils.fromWei(res.ct[8], 'ether');
        }).then(()=>{
          contract.methods.getTokenPrice(Vue.prototype.ArrList.hoyArr,Vue.prototype.ArrList.UsdtArr,'1000000000000000000').call().then((res2) => {
            info.HOYPrice = web3.utils.fromWei(res2, 'ether');
          })
        }).then(()=>{
          contract.methods.getTokenPrice('0x5545153CCFcA01fbd7Dd11C0b23ba694D9509A6F',Vue.prototype.ArrList.UsdtArr,'1000000000000000000').call().then((res5) => {
           
            var num = web3.utils.fromWei(res5, 'ether');
            info.HTPrice = Number(num).toFixed(2)
                        console.log(info)
            commit('set_HTInfoPool',info )
          })
        })
  
      })
    },


  // 
  add_destroyHoyContract: ({ commit }, data) => {
    let info = {};
    getWeb3.Init(deposit_pool, Vue.prototype.ArrList.xhDappArr, (contract, addr, balance) => {
      commit('set_destroyHoyContract', contract)
      commit('set_account', addr);
      commit('set_balance', web3.utils.fromWei(balance, 'ether'));
      info.address = Vue.prototype.ArrList.xhDappArr;
      contract.methods.getMiningInfo(addr).call().then((res) => {
        
        info.info = res[0];
       
      }).then(()=>{
        // HOY
        contract.methods.getTokenPrice(Vue.prototype.ArrList.hoyArr,Vue.prototype.ArrList.UsdtArr,'1000000000000000000').call().then((res2) => {
          info.HOYPrice = web3.utils.fromWei(res2, 'ether');
        })
      }).then(()=>{
        // HbY
        contract.methods.getTokenPrice(Vue.prototype.ArrList.HBYArr,Vue.prototype.ArrList.UsdtArr,'1000000000000000000').call().then((res4) => {
          info.HBYPrice =  web3.utils.fromWei(res4, 'ether');
        })
      }).then(()=>{
        // CHE
        contract.methods.getTokenPrice(Vue.prototype.ArrList.cheArr,Vue.prototype.ArrList.UsdtArr,'1000000000000000000').call().then((res5) => {
          info.CHEPrice =  web3.utils.fromWei(res5, 'ether');
          // console.log(info)
        commit('set_destroyHoyPool',info )
        })
      })
    })
  },


    // HOY
    add_destroyHoyContract2: ({ commit }, data) => {
      let info = {};
      getWeb3.Init(deposit_pool2, '0x1a8793ce67C02Ea0f990F4801BEd49ce43859087', (contract, addr, balance) => {
        commit('set_destroyHoyContract2', contract)
        commit('set_account', addr);
        commit('set_balance', web3.utils.fromWei(balance, 'ether'));
        info.address = '0x1a8793ce67C02Ea0f990F4801BEd49ce43859087';
        contract.methods.getMiningInfo(addr).call().then((res) => {
          
          info.info = res[0];
         
        }).then(()=>{
          // HOY
          contract.methods.getTokenPrice(Vue.prototype.ArrList.hoyArr,Vue.prototype.ArrList.UsdtArr,'1000000000000000000').call().then((res2) => {
            info.HOYPrice = web3.utils.fromWei(res2, 'ether');
            // console.log(info)
            // commit('set_destroyHoyPool2',info )
          })
        }).then(()=>{
          // HOY
          contract.methods.getTokenInfo(Vue.prototype.ArrList.hoyArr).call().then((res3) => {
            info.TokenInfo = res3;
            // console.log(info)
            commit('set_destroyHoyPool2',info )
          })
        })
      })
    },

   // 
   add_HbyDonationContract: ({ commit }, data) => {
    let info = {};
    getWeb3.Init(pool_LP,Vue.prototype.ArrList.HBYDappArr, (contract, addr, balance) => {
      commit('set_HbyDonationContract', contract)
      commit('set_account', addr);
      commit('set_balance', web3.utils.fromWei(balance, 'ether'));
      info.address = Vue.prototype.ArrList.HBYDappArr;
      contract.methods.getUserInfo(addr,0,0).call().then((res) => {
        
        info.info = res[0]; 
      }).then(()=>{
        // console.log(info)
        contract.methods.getTokenPriceMultihop(Vue.prototype.ArrList.HBYArr,Vue.prototype.ArrList.UsdtArr,'1000000000000000000').call().then((res2) => {
        
          info.price = web3.utils.fromWei(res2, 'ether');
          info.price2 = res2;
          console.log(info)
          commit('set_HbyDonationInfo',info )
        })
      })
    })
  },

     // che
     add_CheDonationContract: ({ commit }, data) => {
      let info = {};
      getWeb3.Init(pool_LP, Vue.prototype.ArrList.CHEDappArr, (contract, addr, balance) => {
        commit('set_CheDonationContract', contract)
        // commit('set_account', addr);
        // commit('set_balance', web3.utils.fromWei(balance, 'ether'));
        info.address =Vue.prototype.ArrList.CHEDappArr;
        contract.methods.getUserInfo(addr,0,0).call().then((res) => {
          
          info.info = res[0];
        }).then(()=>{
          contract.methods.getTokenPrice(Vue.prototype.ArrList.cheArr,Vue.prototype.ArrList.UsdtArr,'1000000000000000000').call().then((res2) => {
          
            info.price =web3.utils.fromWei(res2, 'ether');
            info.price2 =res2;
            console.log(info)
            commit('set_CheDonationInfo',info )
          })
        })
      })
    },
  
  //  che
  add_CheCoinContract: ({ commit }, data) => {
    getWeb3.Init(HMDABI, Vue.prototype.ArrList.cheArr, (contract, addr, balance) => {
      commit('set_CheCoinContract', contract);
    })
  },

  //  HBY
  add_HbyCoinContract: ({ commit }, data) => {
    getWeb3.Init(HMDABI, Vue.prototype.ArrList.HBYArr, (contract, addr, balance) => {
      commit('set_HbyCoinContract', contract);
    })
  },
 

  //  USDT+CHE
   add_UsdtCheCoinContract: ({ commit }, data) => {
     let usdt_che={}
    getWeb3.Init(LPABI, '0x573Ff29F67337aBA53F30f9a5EEFEcEdC7c8E19F', (contract, addr, balance) => {
      commit('set_UsdtCheCoinContract', contract);
      contract.methods
          .getReserves()
          .call()
          .then((res) => {
            //LP
            usdt_che.liquidity =res[1];
            // console.log('liquidity=========',res[1],usdt_che.liquidity)
          }).then(()=>{
            contract.methods.totalSupply()
            .call()
            .then((res2) => {
               //LP
              usdt_che.totalSupply = res2
            //  console.log('usdt+CHE==========',usdt_che.liquidity*2)
             commit('set_usdtCheInfo', usdt_che);
            })
          })
    })
  },

    //  HT+CHE
    add_HTCheCoinContract: ({ commit }, data) => {
      let usdt_che={}
     getWeb3.Init(LPABI, '0x66e3FC5468fF9cFEe2AB526E1F21690644f64d66', (contract, addr, balance) => {
       commit('set_HTCheCoinContract', contract);
       contract.methods
           .getReserves()
           .call()
           .then((res) => {
             //LP
            
             var num = web3.utils.fromWei(res[0], 'ether');
             usdt_che.liquidity =Number(num).toFixed(2)
            //  console.log('liquidity=========',res[1],usdt_che.liquidity)
           }).then(()=>{
             contract.methods.totalSupply()
             .call()
             .then((res2) => {
                //LP
               
               var num = web3.utils.fromWei(res2, 'ether');
               usdt_che.totalSupply = Number(num).toFixed(2)
             })
           }).then(()=>{
            contract.methods.price('0x5545153CCFcA01fbd7Dd11C0b23ba694D9509A6F','1000000000000000000')
            .call()
            .then((res3) => {
               //LP
              usdt_che.htPrice = res3
             console.log('usdt+CHE==========',usdt_che)
             commit('set_HTCheInfo', usdt_che);
            })
          })
     })
   },
 // LP
 add_LPCoinContract: ({ commit }, data) => {
  let lp={}
  getWeb3.Init(coin_lp,"0x02701714baC294636866d406C1711f95B8B9eFdc", (contract, addr, balance) => {
    // commit('set_LPCoin', contract);
    contract.methods
    .getReserves()
    .call()
    .then((res) => {
      //LP
      lp.liquidity =res[1];
      // console.log('liquidity=========',res[1],lp.liquidity)
    }).then(()=>{
      contract.methods.totalSupply()
      .call()
      .then((res2) => {
         //LP
         lp.totalSupply = res2
      //  console.log('usdt+CHE==========',lp)
       commit('set_LPInfo', lp);
      })
    })
  })
},
    // HOY
    add_HOYCoinContract: ({ commit }, data) => {
      var HOYinfo={};
      getWeb3.Init(DOT, Vue.prototype.ArrList.hoyArr, (contract, addr, balance) => {
        commit('set_HOYCoinContract', contract);
        contract.methods
          .balanceOf('0x02701714baC294636866d406C1711f95B8B9eFdc')
          .call()
          .then((res) => {
            HOYinfo.dead = res;
           
          }).then(()=>{
            // 
            contract.methods
            .balanceOf(addr)
            .call()
            .then((data) => {
              HOYinfo.balance = data;
              // console.log('HOY=========',HOYinfo)
              commit('set_HOYinfo', HOYinfo)
            });
          })
      })
    },
}
export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}




