            /> </a
        >
        </van-col>
         <van-col span="16" class="textCenter">
            <span v-if="!isHome" style="font-size: 16px;color:#EEBB1E"></span> 
         </van-col>
        <van-col span="4"  @click="showPopup" class="textRight">
          <img src="../assets/img/hoy/mune.png" alt="" width="30"
            />
        </van-col>
      </van-row>
      </div>
      
      <div v-else class="banle" style=" position: fixed;   background:#542D13;z-index:12">
<van-row >
        <van-col span="4">
          <a v-if="isHome" >            <img @click="hreftwo('/')"
              style="width: 100px; margin-left: 5px"
              src="../assets/img/hoy/logo.png"
            /> </a
        >
          <a v-else>
            <img @click="hreftwo('/')"
              style="width: 12px; margin-top: 5px"
              src="../assets/img/hoy/j.png"
            /> </a
        >
        </van-col>
         <van-col span="16" class="textCenter">
            <span v-if="!isHome&&titleHead" style="font-size: 16px;color:#EEBB1E">{{titleHead}}</span> 
         </van-col>
        <van-col span="4"  @click="showPopup" class="textRight">
          <img src="../assets/img/hoy/muse-1.png" alt="" width="25" style="margin-top:3px"
            />
        </van-col>
      </van-row>
      </div>
      
      
     

      <van-popup
        v-model="show"
        position="right"
        :style="{ height: '100%', width: '60%',background:'#542D13' }"
      >
        <div class="menu">
          <img src="../assets/img/hoy/logo.png" alt="" width="55%" />
          <div class="list">
            <div class="li_col">
              <img src="../assets/img/hoy/icon-1.png" alt="" width="20" />
              <a  @click="hreftwo('/')">H-{{ $t("message.f_61_5") }}</a>
            </div>
             <div class="li_col">
              <img src="../assets/img/hoy/icon-2.png" alt="" width="20" />
              <a>H-{{ $t("message.f_62") }}</a>
              <ul>
                <li  @click="hreftwo('/ming_usdt')">USDT-CHE/LP {{ $t("message.f_63") }}</li>
                 <li   @click="hreftwo('/ming_ht')">HT-CHE/LP {{ $t("message.f_63") }}</li>
              </ul>
            </div>
             <div class="li_col">
              <img src="../assets/img/hoy/icon-3.png" alt="" width="20" />
              <a @click="hreftwo('/destroy')">H-{{ $t("message.f_64") }}</a>
              <!-- <ul>
                <li  @click="hreftwo('/destroy_hby')">HBY</li>
                 <li   @click="hreftwo('/destroy_che')">CHE</li>
              </ul> -->
            </div>
            <!-- <div class="li_col">
              <img src="../assets/img/hoy/icon-12.png" alt="" width="20" />
              <a @click="hreftwo('/hoyDestroy')">H-{{ $t("message.f_18") }} HOY {{ $t("message.f_63") }}</a>
            </div> -->
            <div class="li_col">
              <img src="../assets/img/hoy/icon-4.png" alt="" width="20" />
              <a  @click="hreftwo('/donation')">H-{{ $t("message.f_65") }}</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/hoy/icon-5.png" alt="" width="20" />
              <a>H-{{ $t("message.f_61_y") }}</a>
              <ul>
                <li :class="lang == 'zhCHS'?'in':''" @click="changeLocale('zhCHS')">{{ $t("message.f_61_3") }}<img v-if="lang == 'zhCHS'" class="gou" src="../assets/img/hoy/gou.png"/></li>
                 <li :class="lang == 'en'?'in':''" @click="changeLocale('en')" >{{ $t("message.f_61_4") }} <img v-if="lang == 'en'" class="gou" src="../assets/img/hoy/gou.png"/></li>
              </ul>
            </div>
            <div class="li_col">
              <img src="../assets/img/hoy/icon-6.png" alt="" width="20" />
              <a   @click="tips">H-DAO</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/hoy/icon-7.png" alt="" width="20" />
              <a   @click="hreftwo('/donation')">H-NFT</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/hoy/icon-8.png" alt="" width="20" />
              <a  href="https://ht.mdex.co/#/swap?inputCurrency=0xa71EdC38d189767582C38A3145b5873052c3e47a&outputCurrency=0x7dd66d79bc2f1410c29e6c2c13cc21417caddb77">H-{{ $t("message.f_66") }}</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/hoy/icon-9.png" alt="" width="20" />
              <a  href="https://HOYdapp.github.io/HOY.pdf">H-{{ $t("message.f_68") }}</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/hoy/icon-10.png" alt="" width="20" />
              <a href="https://github.com/HOYdapp/HOYdapp.github.io">H-{{ $t("message.f_69") }}</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/hoy/icon-11.png" alt="" width="20" />
              <a href="https://hoydapp.github.io/HOY.pdf">H-{{ $t("message.f_70") }}</a>
            </div>
            <!-- <div class="li_col">
              <img src="../assets/img/hoy/icon-10.png" alt="" width="20" />
              <a href="https://pancakeswap.finance/swap?inputCurrency=0x55d398326f99059ff775485246999027b3197955&outputCurrency=0x3d5821dbcb4c7ecc7b06b8016468e98180e8b638"  target="_blank"></a>
            </div> -->
            <!-- <div class="li_col">
              <img src="../assets/img/hoy/icon-11.png" alt="" width="20" />
              <a href="https://www.dextools.io/app/bsc/pair-explorer/0x3d5821dbcb4c7ecc7b06b8016468e98180e8b638"  target="_blank">K</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/hoy/icon-5.png" alt="" width="20" />
              <a href="https://bscscan.com/address/0x3d5821dbcb4c7ecc7b06b8016468e98180e8b638#code"  target="_blank"></a>
            </div> -->
            </div>
        </div>
      </van-popup>
    </div>
  </header>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      isShowNav: false,
      showRule: false,
      show: false,
      lang_1:null,
      langList: [
        {
          text: "",
          img_txt: "zhCHS",
        },
        {
          text: "EN",
          img_txt: "en",
        },
      ],
    };
  },
  props: {
    lang: {
      type: String,
      default: "zhCHS",
    },
    type: {
      type: String,
      default: "home",
    },
    titleHead: {
      type: String,
      default: " ",
    },
    isHome: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ...mapState("main", ["account", "hmdBalance"]),
  },
  methods: {
    ...mapActions("main", ["add_hmdCoinContract"]),
    showPopup() {
      this.show = true;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
    changeLocale(locale) {
      localStorage.setItem("lang", locale);
      this.$emit("changeAppLocale", locale);
      // this.isShowNav = !this.isShowNav;
    },
    LanguageClick: function () {
      this.isShowNav = !this.isShowNav;
    },
    tips() {
      this.$toast.fail(this.$t("message.tips3"));
      // this.$dialog.alert({
      //   message: this.$t("message.tips3"),
      //   theme: "round-button",
      // });
    },
    prev() {
      this.$router.go(-1);
    },

    // 	showPopup() {
    //   this.showRule = true;
    // },
    hreftwo(url,title) {
      console.log(url);
      // if (url.length > 1) {
      //   this.$router.push({ path: url + "/" + this.lang });
      // } else {
        if(url.length>2){
          this.$router.push({ path: url   });
        }else{
          this.$router.push({ path: url });
        }
      
      // }
    },
  },
};
</script>

<style scoped lang="less">
.banle {
  padding: 20px 3% 10px;
  top: 0;
  width: 100%;
  box-sizing: border-box;
}

.menu {
  text-align: center;
  padding: 20px 0px;
  position: relative;
  height: 90%;
   background: url(../assets/img/hoy/h-bg.jpg) top center no-repeat;
      background-size: 100%;
  .list {
    // border-top: 1px solid #3e210e;
    margin-top: 20px;
    padding:20px;
    img {
      vertical-align: bottom;
      float: left;
      margin-top: 15px;
    }
    a {
      font-size: 16px;
      text-align: left;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 500;
      color: #EEBB1E;
      display: block;
      padding: 12px 30px 12px 30px
    }
  }
   .li_col li.in{color:#FF4F26;}
  .li_col li{
    padding: 5px 10px 5px 50px;
    text-align: left;
    color: #B66701;
    .gou{
          width: 15px;
    float: none;
    margin-top: 0;
    margin-left: 20px;
    }
  }
  .foot {
    position: absolute;
    bottom: 20px;
    left:20px;
    img {
      display: inline-block;
      margin-right: 8px;
    }
  }
}
</style>



